body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 1em;
  font-weight: 500;
  line-height: 1.5em;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  justify-content: space-between;
  align-items: end;
  margin: 0 0 24px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 1em;
  line-height: 150%;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__buy-btn {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding-block: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__buy-btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
